.navbar-collapse{padding: 20px 0;}
.nav-mobile-structure{width: 100%;position: relative;}
.navbar-icon {
  display: block;
  width: 26px;
  height: 20px;
  margin: 0 18px 0 0;
  padding: 0;
  border: none;
  background-color: transparent;
  position: relative;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: .5s ease-in-out;
  cursor: pointer;
}
.navbar-icon span {
  display: block;
  position: absolute;
  height: 4px;
  width: 100%;
  background: #FFFFFF;
  border-radius: 9px;
  opacity: 1;
  left: 0;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}
.navbar-icon span:nth-child(1) {
  top: 0;
}
.navbar-icon span:nth-child(2),.navbar-icon span:nth-child(3) {
  top: 8px;
}
.navbar-icon span:nth-child(4) {
  top: 16px;
}
.navbar-icon.open span:nth-child(1) {
  top: 18px;
  width: 0;
  left: 50%;
}
.navbar-icon.open span:nth-child(2) {
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.navbar-icon.open span:nth-child(3) {
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.navbar-icon.open span:nth-child(4) {
  top: 18px;
  width: 0%;
  left: 50%;
}
@media (min-width: 992px) {
  .nav-mobile-structure{width: auto;}
  .navbar-icon{
    display: none;
  }
}

